import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Button } from "@mui/material";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import "jspdf-autotable";

function CompletedTaskBody(props) {
  let [openDialog, setOpenDialog] = useState(false);
  let [items, setItems] = useState([]);
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });
  let [companies, setCompanies] = useState([]);
  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/gettasks/alreadyCompletedTask", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data.tasks);
        setOpenDialog(false);
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleClick = (event, cellValues) => {
    // console.log(cellValues)
    props.history.push({ pathname: "/approvedSpecificBoq", data: cellValues });
  };

  const handelClickAWR = (event, cellValues) => {
    props.history.push({
      pathname: "/RaiseAWRBody",
    });
  };

  // To get user is Subcon or Mobitel
  const userType = JSON.parse(localStorage.getItem("user")).visbilityBasedOn;
  console.log(userType);
  const columns = [
    { field: "taskRef", headerName: "Task ID", width: 400 },
    { field: "siteID", headerName: "Site ID", width: 130 },
    {
      field: "companyName",
      headerName: "Subcon / Vender",
      width: 250,
      valueGetter: (params) => params.row.assignedSubcon.companyName,
    },
    {
      field: "assignedMobitelOfficer",
      headerName: "Assigned Officer",
      width: 250,
      valueGetter: (params) => params.row.assignedMobitelOfficer.name,
    },
    {
      field: "taskRaisedDate",
      headerName: "PR Raised date",
      width: 200,
      valueGetter: (params) =>
        params.row.taskHistory
          .find((x) => x.taskStatus === "PR Raised")
          .performedDate.split("T")[0],
    },
    { field: "PRnumber", headerName: "PR Number", width: 170 },
    {
      field: "budgetCodeCapex",
      headerName: "Capex Budget Code",
      width: 240,
      valueGetter: (params) => {
        // Test Code-1
        // if (params.row.boqs.length > 0 && params.row.boqs[params.row.boqs.length - 1].budgetCodeCapex) {
        //     return params.row.boqs[params.row.boqs.length - 1].budgetCodeCapex;
        // }
        // else if (params.row.capexopex === 'Copex') {
        //     return params.row.budgetCode;
        // } else if (params.row.budgetCodeCapex) {
        //     return params.row.budgetCodeCapex;
        // } else {
        //     return "N/A";
        // }
        // Test Code-2
        if (params.row.capexopex === "Copex") {
          return params.row.budgetCode || params.row.budgetCodeCapex;
        } else if (params.row.capexopex === "Capex+Opex") {
          return params.row.budgetCodeCapex;
        } else {
          return "N/A";
        }
      },
    },

    {
      field: "budgetCodeOpex",
      headerName: "Opex Budget Code",
      width: 230,
      valueGetter: (params) => {
        // Test Code-1
        // if (params.row.boqs.length > 0 && params.row.boqs[params.row.boqs.length - 1].budgetCodeCapex) {
        //     return params.row.boqs[params.row.boqs.length - 1].budgetCodeCapex;
        // } else if (params.row.capexopex === 'Opex') {
        //     return params.row.budgetCode;
        // } else if (params.row.budgetCodeOpex) {
        //     return params.row.budgetCodeOpex;
        // } else {
        //     return "N/A";
        // }
        // Test Code-2
        if (params.row.capexopex === "Opex") {
          return params.row.budgetCode || params.row.budgetCodeOpex;
        } else if (params.row.capexopex === "Capex+Opex") {
          return params.row.budgetCodeOpex;
        } else {
          return "N/A";
        }
      },
    },
    {
      field: "CapexPrAmount",
      headerName: "Capex Amount",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        // const capexopex = params.row.capexopex;
        // const CPXamount = params.row.CapexPrAmount;
        // const boqs = params.row.boqs;
        // const value1 = boqs.length > 0 ? boqs[boqs.length - 1].CapexPrAmount || 0 : 0;
        // const totalCost = boqs.length > 0 ? boqs[boqs.length - 1].totalCost : 0;
        // const value2 = capexopex === 'Copex' ? totalCost : 0;
        // const value = value1 || value2 || CPXamount || 0;
        // return value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });

        // const totalCost = params.row.totalCost;
        const boqs = params.row.boqs;
        const totalCost = boqs.length > 0 ? boqs[boqs.length - 1].totalCost : 0;
        if (
          params.row.capexopex === "Copex" ||
          params.row.capexopex === "Capex+Opex"
        ) {
          const value =
            params.row.CapexPrAmount > 0 ? params.row.CapexPrAmount : totalCost;
          return value;
          // return value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
        } else {
          return "N/A";
        }
      },
    },
    {
      field: "OpexPrAmount",
      headerName: "Opex Amount",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        // const capexopex = params.row.capexopex;
        // const boqs = params.row.boqs;
        // const value1 = boqs.length > 0 ? boqs[boqs.length - 1].OpexPrAmount || 0 : 0;
        // const totalCost = boqs.length > 0 ? boqs[boqs.length - 1].totalCost : 0;
        // const value2 = capexopex === 'Opex' ? totalCost : 0;
        // const value = value1 || value2 || 0;
        // return value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });

        // const totalCost = params.row.totalCost;
        const boqs = params.row.boqs;
        const totalCost = boqs.length > 0 ? boqs[boqs.length - 1].totalCost : 0;
        if (
          params.row.capexopex === "Opex" ||
          params.row.capexopex === "Capex+Opex"
        ) {
          const value =
            params.row.OpexPrAmount > 0 ? params.row.OpexPrAmount : totalCost;
          return value;
        } else {
          return "N/A";
        }
      },
    },
    {
      field: "totalCost",
      headerName: "Total Value",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        // const value = params.row.boqs[params.row.boqs.length - 1][params.field] || 0;
        const boqs = params.row.boqs;
        const value1 = params.row.totalCost;
        const value2 = boqs.length > 0 ? boqs[boqs.length - 1].totalCost : 0;
        // return value.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
        const value = value1 || value2 || 0;
        return value;
      },
    },
    {
      field: "BOQ",
      width: 110,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleClick(event, cellValues);
            }}
          >
            BOQ
          </Button>
        );
      },
    },
    // {
    //   field: "AWR",
    //   width: 130,
    //   renderCell: (cellValues) => {
    //     return (
    //       <Button
    //         variant="contained"
    //         color="primary"
    //         onClick={(event) => {
    //           handelClickAWR(event, cellValues);
    //         }}
    //       >
    //         AWR
    //       </Button>
    //     );
    //   },
    // },
  ];

  if (userType === "Subcon") {
    // Find the columns with fields 'taskRaisedDate','PRnumber', 'budgetCodeCapex', 'budgetCodeOpex', 'CapexPrAmount', and 'OpexPrAmount' and remove them from the columns array
    const fieldsToRemove = [
      "taskRaisedDate",
      "PRnumber",
      "budgetCodeCapex",
      "budgetCodeOpex",
      "CapexPrAmount",
      "OpexPrAmount",
    ];
    fieldsToRemove.forEach((field) => {
      const index = columns.findIndex((column) => column.field === field);
      if (index !== -1) {
        columns.splice(index, 1);
      }
    });
  }

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenDialog(false);
  };

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>All Completed Tasks</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>
    </div>
  );
}

export default withRouter(CompletedTaskBody);
